<template>
  <div class="home-container">
    <van-nav-bar title="标题" :fixed="true"/>
<!--轮播区域----------------------------------------------------------------------->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item>1</van-swipe-item>
      <van-swipe-item>2</van-swipe-item>
      <van-swipe-item>3</van-swipe-item>
      <van-swipe-item>4</van-swipe-item>
    </van-swipe>


<!--    下拉刷新------------------------------------------------------------------->
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :disabled="finished">
<!--    上拉加载更多---------------------------------------------------------------->
      <van-list
          v-model="loading"
          :finished="finished"
          finished-text="我是有底线的~~"
          @load="onLoad">
        <activeinfo v-for="item in arrlist"
                    :key="item.id"
                    :title="item.title"
                    :mouther="item.aut_name"
                    :count="item.comm_count"
                    :time="item.pubdate"
                    :cover="item.cover">
        </activeinfo>
      </van-list>
    </van-pull-refresh>
  </div>
</template>

<script>
//导入 axios 请求的方法
import $http from "../../utils/request";

//到日 Aciveinfo 组件 注册使用
import Activeinfo from "../../components/activeinfo/Activeinfo";
export default {
  name: "Home",
  components: {Activeinfo},
  comments:{
    Activeinfo
  },
  data(){
    return{
      //页码
      page: 1,

      //每页显示多少条
      limit:10,

      //文章的数据存储
      arrlist:[],

      //是否正在加载下一页数据，如果 loading 为 true ，则不会反复触发 load 事件
      //注意：每当下一页数据请求回来之后，一定要记得， 把 loading 从 true 改为 false
      loading:true,

      //所有数据是否加载完毕,乳沟没有更多数据了， 一定要把 finished 改为 true
      finished:false,

      //是否正在下拉刷新
      isLoading:false
    }
  },
  //--------------------------------------------------------------------
  created() {
    this.inithttp()
  },
  //-------------------------------------------------------------------
  methods:{
    //封装获取文章列表数据的方法
    async inithttp(isrefresh){
      const {data:res} = await $http.get('/articles',{
        params:{
          _page:this.page,
          _limit:this.limit
        }
      })
      //------------------------------------------------
      if (isrefresh) {
      //证明是下拉刷新
        //this.arrlist = [旧数据在前，新数据在后]
        this.arrlist = [...res, ...this.arrlist]
        this.isLoading = false
      }else{
      //证明是上拉加载更多
        //如果是上拉加载更多，那应该是旧数据在前，新数据在后
        //this.arrlist = [旧数据在前，新数据在后]
        this.arrlist = [...this.arrlist , ...res]
        this.loading = false
      }
      //----------------------------------------------------
      //判断数据是否加载完毕,如果请求的数据为空，表示全部数据请求完了
      if(res.length == 0){
        this.finished = true
      }
      // console.log(this.arrlist)
    },

 //---------------------------------------------------------------------------
    //下拉刷新的处理函数
    //只要 onload 被调用，就应该请求下一页数据
    onLoad() {
      console.log('触发了onload事件')
      //1.让页码 +1
      this.page++
      //2.重新请求接口获取数据
      this.inithttp()
    },

  //--------------------------------------------------------------------------
    //下拉刷新的处理函数
    onRefresh(){
      console.log('触发了刷新')
      //1.让页码 +1
      this.page++
      //2.重新请求接口获取数据
      this.inithttp(true)
    }
  }
}
</script>

<style scoped>
.home-container{
  padding: 46px 0 50px 0;
}
.van-nav-bar{
  background-color: aqua;
}
.van-nav-bar__title{
  color: white;
}
/*轮播图*/
.my-swipe .van-swipe-item {
  color: #fff;
  font-size: 20px;
  line-height: 150px;
  text-align: center;
  background-color: #39a9ed;
}
/*结束轮播图*/
</style>