<template>
  <div>
    <van-cell>
<!--      标题区域的插槽-->
      <template #title>
        <div class="title-box">
<!--          标题 -->
          <span>{{ title }}</span>
<!--          单张图片-->
          <img :src="cover.images[0]" alt="" class="thumb" v-if="cover.type === 1">
        </div>

<!--        三张图-->
        <div class="thumb-box" v-if="cover.type === 3">
          <img :src="item" alt="" class="thumb" v-for="(item , i) in cover.images" :key="i">
        </div>
      </template>

<!--      label区域的插槽-->
      <template #label>
        <div class="label-box">
          <span>作者{{mouther}} &nbsp;&nbsp;{{count}}评论&nbsp;&nbsp;发布时间{{time}}</span>
          <!--        关闭按钮-->
          <van-icon name="cross" />
        </div>
      </template>
    </van-cell>
  </div>
</template>

<script>
export default {
  name: "Activeinfo",
  props:{
    //文章的标题
    title:{
      type:String,
      default:''
    },
    //作者
    mouther:{
      type:String,
      default: ''
    },
    //评论数
    count:{
      type:String,
      default:0
    },
    //发布时间
    time:{
      type:String,
      default:''
    },
    //封面的信息对象
    cover:{
      type:Object,
      default: () =>{
        return { type:0 }
      }
    }
  }
}
</script>

<style scoped>
.label-box{
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.thumb{
  width: 113px;
  height: 70px;
  background-color: #f8f8f8;
  object-fit: cover;
}
.title-box{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.thumb-box{
  display: flex;
  justify-content: space-between;
}
</style>